import React, { useEffect, useState } from 'react'
import { useHistory } from 'react-router-dom'
import { tagPageView, tagEvent } from '../Common/TagManager'

import Grid from '@material-ui/core/Grid'

import { gameData } from "./GameData"
import GameTile from './GameTile'

import './Home.css'

function Home () {
    const lang = 'he'
    const history = useHistory()
    const [categories, setCategories] = useState([])

    tagPageView('/app', 'choose category')

    
    const handleGameTileClick = (value) => {
        console.log('gameTileClick(home): ' + value)

        tagEvent('game', 'category', value)

        history.push({
            pathname: '/app/level',
            search: '?category=' + value + '&lang=' + lang,
            state: { category: value }
        })
    }

    useEffect(() => {
        localStorage.removeItem('wordsSolvedArray')
        localStorage.removeItem('currentWordTextFieldsSolved')

        const initCategoriesArray = () => {
            /*
            category: {
                title: {
                    he: {
                        title: "בַּבַּיִת"
                    },
                    en: {
                        title: "home"
                    }
                },
                value: "home"
        }
            */
            if (categories.length <= 0) {
                const tempCategories = new Array(gameData.length)

                for (var i=0; i < gameData.length; i++) {
                    const category = gameData[i].category
                    tempCategories[i] = category
                }
                setCategories(tempCategories)
            }
        }
        initCategoriesArray()
    },[categories]);
    
    return (
        <div className='home'>
            <div className='game-body'>
            
            
                <Grid style={{marginTop:0}} container direction='row' justify='center' alignItems='center' spacing={3}>
                    <Grid item xs={6} sm={3}>
                        <GameTile gameTile={{ value:'fairy-tale', title:'אַגָּדוֹת', imageurl: process.env.PUBLIC_URL + '/images/fairy-tale/frog.svg' }} onGameTileClick={handleGameTileClick}></GameTile>
                    </Grid>
                    <Grid item xs={6} sm={3}>
                        <GameTile gameTile={{ value:'love', title:'אַהֲבָה', imageurl: process.env.PUBLIC_URL + '/images/love/heart.svg' }} onGameTileClick={handleGameTileClick}></GameTile>
                    </Grid>
                    <Grid item xs={6} sm={3}>
                        <GameTile gameTile={{ value:'food', title:'אֹכֶל', imageurl: process.env.PUBLIC_URL + '/images/food/toast.svg' }} onGameTileClick={handleGameTileClick}></GameTile>
                    </Grid>
                    <Grid item xs={6} sm={3}>
                        <GameTile gameTile={{ value:'home', title:'בַּבַּיִת', imageurl: process.env.PUBLIC_URL + '/images/home/image.svg' }} onGameTileClick={handleGameTileClick}></GameTile>
                    </Grid>
                </Grid>
                <Grid style={{marginTop:12}} container direction='row' justify='center' alignItems='center' spacing={3}>
                    <Grid item xs={6} sm={3}>
                        <GameTile gameTile={{ value:'animals', title:'בַּעֲלֵי חַיִּים', imageurl: process.env.PUBLIC_URL + '/images/animals/giraffe.svg' }} onGameTileClick={handleGameTileClick}></GameTile>
                    </Grid>
                    <Grid item xs={6} sm={3}>
                        <GameTile gameTile={{ value:'fruits-and-vegetables', title:'יְרָקוֹת וּפֵרוֹת', imageurl: process.env.PUBLIC_URL + '/images/fruits-and-vegetables/027-strawberry.svg' }} onGameTileClick={handleGameTileClick}></GameTile>
                    </Grid>
                    <Grid item xs={6} sm={3}>
                        <GameTile gameTile={{ value:'tools', title:'כֵּלִים', imageurl: process.env.PUBLIC_URL +'/images/tools/hammer.svg' }} onGameTileClick={handleGameTileClick}></GameTile>
                    </Grid>
                    <Grid item xs={6} sm={3}>
                        <GameTile gameTile={{ value:'science', title:'מַדָּע וְחָלָל', imageurl: process.env.PUBLIC_URL + '/images/science/planet-earth.svg' }} onGameTileClick={handleGameTileClick}></GameTile>
                    </Grid>
                </Grid>
                <Grid style={{marginTop:12}} container direction='row' justify='center' alignItems='center' spacing={3}>
                    <Grid item xs={6} sm={3}>
                        <GameTile gameTile={{ value:'kitchen', title:'מִטְבָּח', imageurl: process.env.PUBLIC_URL + '/images/kitchen/oven-mitts.svg' }} onGameTileClick={handleGameTileClick}></GameTile>
                    </Grid>
                    <Grid item xs={6} sm={3}>
                        <GameTile gameTile={{ value:'devices', title:'מַכְשִׁירִים', imageurl: process.env.PUBLIC_URL + '/images/devices/desk-lamp.svg' }} onGameTileClick={handleGameTileClick}></GameTile>
                    </Grid>
                    <Grid item xs={6} sm={3}>
                        <GameTile gameTile={{ value:'medicine', title:'רְפוּאָה', imageurl: process.env.PUBLIC_URL + '/images/medicine/hospital.svg' }} onGameTileClick={handleGameTileClick}></GameTile>
                    </Grid>
                    <Grid item xs={6} sm={3}>
                        <GameTile gameTile={{ value:'transport', title:'תַּחְבּוּרָה', imageurl: process.env.PUBLIC_URL + '/images/transport/aeroplane.svg' }} onGameTileClick={handleGameTileClick}></GameTile>
                    </Grid>
                </Grid>
            </div>
        </div>
    )
}

export default Home
